  padding: 7px 25px 7px 0;
  font-family: "Lato", sans-serif;
  font-weight: 300;
  font-size: 1.55rem;
  font-weight: 500;
  vertical-align: middle;
  margin: 0 15px 0 0;
  color: #3f4047;
}

.m-subheader .m-subheader__title.m-subheader__title--separator {
  border-right: 1px solid #e2e5ec;
}

.m-subheader .m-subheader__breadcrumbs.m-nav > .m-nav__item {
  padding: 0;
}

.m-subheader
  .m-subheader__breadcrumbs.m-nav
  > .m-nav__item
  > .m-nav__link
  > .m-nav__link-text {
  color: #898b96;
}

.m-subheader
  .m-subheader__breadcrumbs.m-nav
  > .m-nav__item
  > .m-nav__link
  > .m-nav__link-icon {
  font-size: 1.3rem;
  color: #898b96;
}

.m-subheader
  .m-subheader__breadcrumbs.m-nav
  > .m-nav__item
  > .m-nav__link:hover
  > .m-nav__link-text {
  color: #009d3b;
}

.m-subheader
  .m-subheader__breadcrumbs.m-nav
  > .m-nav__item
  > .m-nav__link:hover
  > .m-nav__link-icon {
  color: #009d3b;
}

.m-subheader
  .m-subheader__breadcrumbs.m-nav
  > .m-nav__item.m-nav__item--home
  > .m-nav__link
  > .m-nav__link-icon {
  padding-right: 0;
}

.m-subheader .m-subheader__breadcrumbs.m-nav > .m-nav__separator {
  padding: 0 3px;
  color: #898b96;
}

.m-subheader .m-subheader__daterange {
  display: inline-block;
  border-radius: 2rem;
  padding: 5px;
  cursor: pointer;
  background: #fff;
}

.m-subheader .m-subheader__daterange .m-subheader__daterange-label {
  padding: 0.7rem 0.75rem 0.7rem 1.5rem;
}

.m-subheader
  .m-subheader__daterange
  .m-subheader__daterange-label
  .m-subheader__daterange-title {
  display: inline-block;
  font-weight: 400;
  color: #aaaeb8;
}

.m-subheader
  .m-subheader__daterange
  .m-subheader__daterange-label
  .m-subheader__daterange-date {
