.m-footer {
  position: relative;
  width: auto;
  padding: 7px 30px;
  height: 60px;
  min-height: 60px;
  background: #fff;
  -webkit-box-shadow: -2px -8px 41px -14px rgba(202, 210, 222, 0.61);
  box-shadow: -2px -8px 41px -14px rgba(202, 210, 222, 0.61);
}

.m-footer .m-footer__copyright {
  font-size: 0.9rem;
  font-weight: 400;
  color: #a9a9aa;
}

@media (min-width: 1025px) {
  .m-footer--fixed .m-footer {
    z-index: 102;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    -webkit-transition: padding-top none;
    -o-transition: padding-top none;
    transition: padding-top none;
  }

  .m-footer--fixed .m-body {
    padding-bottom: 60px;
  }

  .m-footer--fixed .m-content {
    padding-bottom: 60px;
  }

  .m-footer--fixed.m-footer--push .m-body {
    padding-bottom: 0;
  }

  .m-footer--fixed.m-footer--push.m-aside-left--enabled .m-footer {
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
    left: 255px;
  }

  .m-footer--fixed.m-footer--push.m-aside-left--enabled.m-aside-left--minimize
    .m-footer {
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
    left: 80px;
  }

  .m-aside-left--hide .m-footer {
    margin-left: 0 !important;
  }

  .m-footer--push.m-aside-left--enabled:not(.m-footer--fixed) .m-footer {
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
    margin-left: 255px;
    margin-top: -60px;
  }

  .m-footer--push.m-aside-left--enabled:not(.m-footer--fixed).m-aside-left--minimize
    .m-footer {
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
    margin-left: 80px;
  }

  .m-footer--push.m-aside-left--enabled:not(.m-footer--fixed) .m-aside-right,
  .m-footer--push.m-aside-left--enabled:not(.m-footer--fixed) .m-wrapper {
    margin-bottom: 60px;
  }
}

@media (min-width: 1025px) and (max-width: 1200px) {
  .m-footer .m-footer__nav .m-nav__item {
    padding: 0 0 0 5px;
  }

  .m-footer .m-footer__nav .m-nav__item:first-child {
    padding-left: 0;
  }
}

@media (max-width: 1024px) {
  .m-footer {
    height: auto;
    padding: 7px 20px;
  }

  .m-footer .m-stack__item {
    text-align: center;
  }

  .m-footer .m-footer__copyright {
    display: inline-block;
    margin-bottom: 1rem;
  }

  .m-footer .m-footer__nav {
    text-align: center;
    float: none;
    margin: 0 auto;
  }

  .m-footer .m-footer__nav .m-nav__item {
    padding: 0 0 0 5px;
  }
}

.m-datatable
  .m-datatable__table
  .m-datatable__body
  .m-datatable__row
  .m-datatable__cell
  img {
  width: 80px;
}

.m-topbar .m-topbar__nav.m-nav > .m-nav__item:hover .m-dropdown__wrapper {
  display: block;
}

@media screen and (max-width: 1199px) {
  .m--align-right {
    text-align: left;
  }
  .m--align-right .m-separator.m-separator--dashed {
    display: none;
  }
  .m-form .m-form__group.m-form__group--inline > .m-form__label {
    display: block;
    margin-bottom: 5px;
  }
}

@media screen and (max-width: 1024px) {
  .m--align-right {
    margin-top: 10px;
  }
  .m-stack.m-stack--general.m-stack--ver
    > .m-stack__item.m-stack__item--middle {
    padding-right: 35px;
  }

  .m-topbar .m-topbar__nav.m-nav > .m-nav__item .m-dropdown__wrapper {
    right: 0;
    left: auto;
  }
}

.m-form__group.m-form__group--inline input[type="file"] {
  height: 40px;
  padding-top: 8px;
}

.err-message p {
  padding-left: 20px;
}

/* Add a green text color and a checkmark when the requirements are right */
.pass-valid {
  color: green;
}

.pass-valid:before {
  position: relative;
  left: -10px;
  content: "";
}

/* Add a red text color and an "x" when the requirements are wrong */
.pass-invalid {
  color: red;
}

.pass-invalid:before {
  position: relative;
  left: -10px;
  content: "";
}



